.schedule-item{display:flex;flex-direction:column;align-items:center;line-height:1.5rem;font-family:"PT Sans",sans-serif;width:100%}.schedule-item__top-div{font-family:"Poppins",sans-serif;display:flex;align-items:center;justify-content:space-between;align-items:baseline;width:100%;padding:.5rem}.schedule-item__top-button{border:none;padding:0;overflow:visible;background:transparent;font-family:"Poppins",sans-serif;text-align:start;display:flex;justify-content:space-between;align-items:baseline;width:100%;font:inherit;line-height:normal}.schedule-item__header{color:var(--header-color)}.schedule-item__subheader{color:var(--sub-header-color)}.schedule-item__hr{width:100%;border:1px solid var(--hr-color);margin:0 .5rem 0 .5rem}.schedule-item__chevron{fill:var(--fill-color);width:1.25rem;height:1.25rem;-webkit-transform:rotate(90deg);transform:rotate(90deg);transition:.3s}.schedule-item__chevron--rotated{-webkit-transform:rotate(270deg);transform:rotate(270deg)}.schedule-item__chevron--hidden{display:none}
