.settings-item-toggle{display:flex;justify-content:space-between;align-items:center;border-bottom:1px solid var(--hr-color);margin:0 2.5rem;padding:1rem 0;position:relative;top:1em}.settings-item-toggle__label{font-weight:700;margin:0}.settings-item-toggle__switch{width:2.813rem;height:1.5rem;position:relative;display:inline-block}.settings-item-toggle__checkbox{opacity:0;width:0;height:0}.settings-item-toggle__checkbox:checked+.settings-item-toggle__slider{background-color:var(--theme-color-primary)}.settings-item-toggle__checkbox:checked+.settings-item-toggle__slider:before{-webkit-transform:translateX(1.2rem);transform:translateX(1.2rem)}.settings-item-toggle__slider{position:absolute;cursor:pointer;top:0;left:0;right:0;bottom:0;background-color:#eaeaea;transition:.4s;border-radius:34px}.settings-item-toggle__slider:before{position:absolute;content:"";height:1.3rem;width:1.3rem;left:2px;bottom:2px;background-color:#fff;transition:.4s;border-radius:50%}
